import React from 'react';
import { Icon } from 'antd';
import resume from './jamesResume.pdf'
import '../../App.css';

const Menu = (props) => {
    return (
        <div className="menuOps">
            <p className='contactTextTitle'><Icon type="mail" /> JFschmiechen@gmail.com</p>
            <a className="op" onClick={(e) => { e.preventDefault(); document.getElementsByClassName('expText')[0].scrollIntoView({ behavior: 'smooth' })}}>
                <p className="iconText">Projects</p>
            </a>
            <a target='_blank' href={resume} className="op">
                <p className="iconText">Resume</p>
            </a>
            <a href='https://linkedin.com/in/james-schmiechen-716847128' className="op">
                <p className="iconText">LinkedIn</p>
            </a>
            <a href="https://github.com/JFschmiechen" className="op">
                <p className="iconText">Github</p>
            </a>
        </div>
    )
};

export default Menu;