import React from 'react';
import { Carousel } from 'antd';

const displayCase = props => {
    const { type, title, subHeader, imgArray } = props;
    const _className = imgArray.length <= 3 ? 'imgBlock' : 'imgBlockDouble';

    return (
        <div>
            <div className='expText' style={{ marginBottom: 0, marginTop: '5%' }}>
                <p style={{ marginBottom: -5 }}>{type}</p>
                <p style={{ fontSize: '2em'}}>{title}</p>
                <p style={{ fontSize: '1.5em' }}>{subHeader}</p>
            </div>
            <div>
                <Carousel>
                    <div>
                        <div>
                            {imgArray.length <= 3
                                ? <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[0]} />
                                </div>

                                : <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[0]} />
                                    <img className={_className} src={imgArray[1]} />
                                </div>
                            }
                        </div>
                    </div>
                    <div>
                        <div>
                            {imgArray.length <= 3
                                ? <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[1]} />
                                </div>

                                : <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[2]} />
                                    <img className={_className} src={imgArray[3]} />
                                </div>
                            }
                        </div>
                    </div>
                    <div>
                        <div>
                            {imgArray.length <= 3
                                ? <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[2]} />
                                </div>

                                : <div className='imgBlockContainer'>
                                    <img className={_className} src={imgArray[4]} />
                                    <img className={_className} src={imgArray[5]} />
                                </div>
                            }
                        </div>
                    </div>
                </Carousel>
            </div>
        </div>
    )
};

export default displayCase;