import React from 'react';
import { Card } from 'antd';

const Profile = (props) => {
    const { image } = props;
    const { Meta } = Card;
    return (
        <Card
            className="profile"
            hoverable
            cover={
                <img src={image} />
            }>
            <Meta
            title="James Schmiechen"
            description="I love everything to do with computers and software"
            />
        </Card>
    )
};

export default Profile;