import React from 'react';
import { Icon } from 'antd'

const Pluses = (props) => {
    return (
        <div className="pluses">
            <div className="plusContainer">
                <Icon className="plusesIcon" type="mobile" />
                <p style={{ fontSize: '1.5em' }}>Mobile friendly web design so you can view on the go</p>
            </div>
            <div className="plusContainer">
                <Icon className="plusesIcon" type="safety" />
                <p style={{ fontSize: '1.5em' }}>Experience building HIPAA compliant software</p>
            </div>
            <div className="plusContainer">
                <Icon className="plusesIcon" type="dashboard" />
                <p style={{ fontSize: '1.5em' }}>Performant UI/UX means fast load times</p>
            </div>
        </div>
    )
};

export default Pluses;