import React from 'react';
import { Progress, Tag, Steps } from 'antd';

const { Step } = Steps;

const Skills = (props) => {
    return (
        <div>
            <div className='skillsContainer'>
                <div className='skillsDescription'>
                    <p style={{ marginBottom: -5, marginTop: 5 }}>Skills</p>
                    <p className='skillTitle' style={{ textAlign: 'left' }}>Learning never ends</p>
                    <p className="skillText">
                        I am driven to bring a tasteful, fresh, and professional experience to every opportunity I have to program. One of my core values is to never stop improving on myself which is why my learning continues outside of the classroom and the job. I am self taught and found myself drawn to numerous facets of programming, but I have found my home with web applications. The internet plays a major part in my life, and I am excited to bring that passion to you.
                    </p>
                </div>
                <div className='progressBarContainer'>
                    <span className="progressContainer">
                        <Tag className="skillTag">HTML</Tag>
                        <Progress  percent={80} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">CSS</Tag>
                        <Progress percent={70} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">React</Tag>
                        <Progress percent={80} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Redux</Tag>
                        <Progress percent={50} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Node</Tag>
                        <Progress percent={70} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Express</Tag>
                        <Progress percent={60} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Postgres</Tag>
                        <Progress percent={40} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Firebase</Tag>
                        <Progress percent={50} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">GraphQL</Tag>
                        <Progress percent={80} />
                    </span>
                    <span className="progressContainer">
                        <Tag className="skillTag">Electron</Tag>
                        <Progress percent={75} />
                    </span>
                </div>
            </div>
            <div className='timelineContainer'>
                <Steps className='timeline' direction='horizontal'  size="small" current={4}>
                    <Step title="Fall 2014" description="Started college as a CS major" />
                    <Step title="Fall 2017" description="Shadowed at A3 Concepts" />
                    <Step title="Fall 2017-18" description="Interning at Onstack Software" />
                    <Step title="Fall 2018-19" description="Interning at BC" />
                    <Step title="Fall 2019" description="Transfer to SFSU" />
                </Steps>
            </div>
        </div>
    )
};

export default Skills;